.testimonials {
  padding: 97px;
  padding-bottom: 0;
  background-color: #f4f5f7;

  @include desktop {
    padding: 97px 50px 0 80px;
  }
  @include tablet {
    padding: 97px 50px 0 20px;
  }

  @include small-tablet {
    padding: 97px 50px 0 80px;
  }

  @include mobile {
    padding: 20px 14px 0 20px;
  }
  &__container {
    display: flex;
    justify-content: space-between;

    @include small-tablet {
      justify-content: center;
    }
  }
  &__content {
    max-width: 600px;
    @include tablet {
      max-width: 423px;
    }
    @include mobile {
      max-width: 261px;
    }
  }

  &__title {
    margin-bottom: 60px;

    @include mobile {
      margin-bottom: 32px;
      font-size: 22px;
    }
  }

  &__items {
    position: relative;
  }

  &__image {
    margin-bottom: -60px;
    z-index: 20;
    @include desktop {
      max-width: 300px;
    }
    @include small-tablet {
      display: none;
    }
  }

  &__slider-nav {
    bottom: 23px;
    left: 490px;
    @include tablet {
      left: 312px;
    }
    @include mobile {
      left: 171px;
    }
  }

  &__prev {
    margin-right: 10px;
  }

  .slider-nav .swiper-button-next,
  .slider-nav .swiper-button-prev {
    position: inherit;
    left: initial;
  }
  .swiper-button-next,
  .swiper-button-prev {
    top: 0;
  }
}

.testimonials-item {
  &__img {
    width: 72px;
    height: 72px;
    overflow: hidden;
    border-radius: 100%;
    margin-bottom: 25px;
    img {
      display: block;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  &__descr {
    margin: 0px;
    font-size: 16px;
    line-height: 160%;
    font-weight: 400;
    color: var(--color-gray);
    margin-bottom: 20px;

    @include mobile {
      font-size: 13px;
      line-height: 148%;

    }
  }

  &__anthor {
    margin-bottom: 4px;
    display: block;
    font-size: 16px;
    line-height: 160%;
    font-weight: 700;
    color: var(--color-ebony);
  }
  &__author-post {
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-storm);
  }
}
